// src/components/EditFocusModal.js
import React, { useState, useMemo, useEffect } from 'react';
import { useHandlers } from '../context/HandlerContext';
import AvailableTestCard from './AvailableTestCard';

const EditFocusModal = ({ isOpen, onClose, testData, todayFocusItems }) => {
    const handlers = useHandlers();
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedAvailableSubject, setSelectedAvailableSubject] = useState('');

    // Reset filters when modal opens
    useEffect(() => {
        if (isOpen) {
            setSearchQuery('');
            setSelectedAvailableSubject('');
        }
    }, [isOpen]);

    // Memoize allTests calculation to prevent unnecessary re-renders
    const allTests = useMemo(() => {
        return Object.keys(testData).flatMap(subject =>
            testData[subject].map(test => ({ ...test, subject, id: `${subject}-${test.title}` }))
        );
    }, [testData]);

    // Memoize filteredAllTests calculation
    const filteredAllTests = useMemo(() => {
        return allTests.filter(test => {
            const matchesSubject = selectedAvailableSubject === '' || test.subject === selectedAvailableSubject;
            const matchesSearch = test.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
                test.subject.toLowerCase().includes(searchQuery.toLowerCase());
            return matchesSubject && matchesSearch;
        });
    }, [allTests, selectedAvailableSubject, searchQuery]);

    const subjects = Object.keys(testData);

    if (!isOpen) {
        return null;
    }

    return (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center p-4 z-50">
            <div className="bg-white rounded-2xl shadow-xl p-6 w-full max-w-md mx-auto relative transform transition-all scale-100 opacity-100 flex flex-col" style={{maxHeight: '85vh'}}>
                <div className="flex justify-between items-center mb-4 pb-4 border-b border-gray-200">
                    <h2 className="text-2xl font-bold text-gray-800">Add to Today's Focus</h2>
                    <button
                        onClick={onClose}
                        className="p-2 rounded-full text-gray-500 hover:bg-gray-200 transition-colors"
                        aria-label="Close modal"
                    >
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
                    </button>
                </div>

                <div className="mb-4">
                    <label htmlFor="select-available-subject" className="block text-sm font-medium text-gray-700 mb-1">Filter by Subject:</label>
                    <select
                        id="select-available-subject"
                        className="w-full px-4 py-2 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 bg-gray-50"
                        value={selectedAvailableSubject}
                        onChange={(e) => setSelectedAvailableSubject(e.target.value)}
                    >
                        <option value="">All Subjects</option>
                        {subjects.map(subject => (
                            <option key={subject} value={subject}>{subject}</option>
                        ))}
                    </select>
                </div>

                <div className="mb-4">
                    <input
                        type="text"
                        placeholder="Search available tests..."
                        className="w-full px-4 py-2 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 bg-gray-50"
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        aria-label="Search available tests"
                    />
                </div>

                <div id="available-tests-list" className="space-y-3 pr-2 -mr-2 overflow-y-auto flex-grow">
                    {filteredAllTests.length === 0 ? (
                        <p className="text-gray-500 text-center py-4">No tests available matching your filters.</p>
                    ) : (
                        filteredAllTests.map(test => {
                            const isAdded = todayFocusItems.some(item => item.id === test.id || (item.subject === test.subject && item.title === test.title));
                            return (
                                <AvailableTestCard
                                    key={test.id}
                                    test={test}
                                    subject={test.subject}
                                    isAdded={isAdded}
                                    onAdd={handlers.addFocusItem}
                                    onRemove={handlers.removeFocusItem}
                                    handleToggleTestCompletion={handlers.toggleTestCompletion}
                                />
                            );
                        })
                    )}
                </div>
                 <div className="mt-6 flex justify-end pt-4 border-t border-gray-200">
                    <button
                        onClick={onClose}
                        className="px-6 py-2 bg-blue-600 text-white font-bold rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-300"
                    >
                        Done
                    </button>
                </div>
            </div>
        </div>
    );
};

export default EditFocusModal;