// src/components/AvailableTestCard.js
import React from 'react';
import { useHandlers } from '../context/HandlerContext';

const AvailableTestCard = ({ test, subject, isAdded }) => {
    const handlers = useHandlers();

    const handleAddClick = (e) => {
        e.stopPropagation();
        handlers.addFocusItem(test, subject);
    };

    const handleRemoveClick = (e) => {
        e.stopPropagation();
        // Find the matching item in today's focus to get its specific ID for removal
        const itemToRemove = { id: test.id };
        if (itemToRemove) {
            handlers.removeFocusItem(itemToRemove.id);
        }
    };
    
    const handleCheckboxClick = (e) => {
        e.stopPropagation();
        handlers.toggleTestCompletion(subject, test.title, !test.completed);
    };

    const cardClasses = `
        flex items-center justify-between p-3 rounded-xl border transition-all duration-300
        ${test.completed 
            ? 'bg-green-50 border-green-200' 
            : 'bg-white border-gray-200 hover:shadow-md'
        }
    `;

    const titleClasses = `
        font-semibold
        ${test.completed 
            ? 'line-through text-gray-500' 
            : 'text-gray-800'
        }
    `;
    
    const subjectClasses = `
        text-sm
        ${test.completed 
            ? 'text-gray-400' 
            : 'text-gray-500'
        }
    `;

    return (
        <div id={`available-test-${test.id}`} className={cardClasses}>
            <div className="flex items-center flex-grow mr-2">
                <button
                    onClick={handleCheckboxClick}
                    className={`w-6 h-6 rounded-md border-2 ${test.completed ? "bg-green-500 border-green-500" : "border-gray-300"} mr-3 flex-shrink-0 flex items-center justify-center transition-colors`}
                    aria-label={test.completed ? "Mark as incomplete" : "Mark as complete"}
                >
                    {test.completed && (
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M5 13l4 4L19 7" />
                        </svg>
                    )}
                </button>
                <div className="truncate">
                    <h4 className={titleClasses}>{test.title}</h4>
                    <p className={subjectClasses}>{subject}</p>
                </div>
            </div>
            
            <button
                onClick={isAdded ? handleRemoveClick : handleAddClick}
                className={`flex-shrink-0 px-4 py-1.5 text-sm font-semibold rounded-lg transition-colors duration-200
                    ${isAdded 
                        ? 'bg-red-100 text-red-700 hover:bg-red-200' 
                        : 'bg-blue-100 text-blue-700 hover:bg-blue-200'
                    }`
                }
            >
                {isAdded ? 'Remove' : 'Add'}
            </button>
        </div>
    );
};

export default AvailableTestCard;